import './FabricantePuntosRetiro.css'

import React from 'react'
import { observer } from 'mobx-react-lite'
import { useStores } from '../../models/root-store/root-store-context'
import * as FIcon from 'react-feather'
import { RUTAS_APP_FABRICANTE } from '../../constants'
import { RouteComponentProps } from 'react-router'
import { useGetCiudadNombre, useOpenPage } from '../../helpers-ts'
import { Page } from '../../components'
import { ICONOS } from '../../helpers-ts/iconos'
import { SubHeader } from '../../components/SubHeader'

export const FabricantePuntosRetiro: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const openPage = useOpenPage()
  const getCiudadNombre = useGetCiudadNombre()
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  React.useEffect(() => {
    obtenerCiudades()
  }, [])

  const obtenerCiudades = () => {
    if (!rootStore.ciudades.length) {
      rootStore.actualizarDatosInicio(false)
    }
  }

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'puntos-retiro-fabricante'}>
      <SubHeader links={[{ titulo: 'Perfil', ruta: RUTAS_APP_FABRICANTE.PERFIL }, { titulo: 'Puntos de retiro' }]} />
      <div className={`pedidos-list-container pt-4 ${Movil ? "container-lg" : ""}`}>
        <div className="titulo text-center text-lg-left">Puntos de Retiro</div>
        {loginStore.fabricante &&
          loginStore.fabricante.puntos_retiro &&
          loginStore.fabricante.puntos_retiro.map((item) => {
            const ciudad = item.ciudad ? getCiudadNombre(item.ciudad) : ''
            return (
              <div key={'punto-retiro-' + item.id} className="col-12">
                <div className={'punto-list-item py-2 mt-5 '}>
                  <div>
                    <span className="icono-punto-retiro">{ICONOS.punto_retiro}</span>

                    <div className="datos-punto ">
                      <strong>{item.nombre}</strong>
                      <br />
                      <span>
                        {item.calle_principal} {item.numero} {item.calle_secundaria && <span>Esq. {item.calle_secundaria}</span>}
                      </span>

                      {ciudad && <span>{ciudad}</span>}
                    </div>
                    <div className="" onClick={openPage(RUTAS_APP_FABRICANTE.PUNTOS_RETIRO_UPDATE(item.id))} style={{width: 25}}>
                      {ICONOS.editar}
                    </div>
                  </div>
                </div>
              </div>
            )
          })}
        {loginStore.fabricante &&
          loginStore.fabricante.puntos_retiro &&
          loginStore.fabricante.puntos_retiro.length === 0 && (
            <div className={'d-flex mt-5'}>
              <span className={'mx-auto my-auto'}>No se encontraron puntos de retiro</span>
            </div>
          )}
        <div className='mt-4'>
          <div className='btn btn-primary px-4' onClick={() => history.push(RUTAS_APP_FABRICANTE.PUNTOS_RETIRO_CREATE)}>Añadir</div>
        </div>
      </div>
    </Page>
  )
})

export default FabricantePuntosRetiro
